@import "../../styles/mixins/media.scss";

.linkWrapper {
  margin-top: 30px;
  margin-bottom: 20px;
}

.link {
  text-decoration: none;
  font-size: 14px;
  line-height: 16px;
  color: var(--accent);

  @include tabletAndDesktop {
    font-size: 15px;
    line-height: 17px;
  }

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.titleWrapper {
  display: flex;
  margin-top: 20px;
  margin-bottom: 30px;
}

.filterBtn {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: var(--accent);
  border: none;
  border-radius: 5px;
  gap: 5px;
  font-size: 14px;
  line-height: 16px;
  color: var(--white);
  margin: 30px 0;

  &:hover,
  &:focus {
    background-color: var(--hover);
  }
}

.btnWrapper {
  display: flex;
  justify-content: center;

  margin-top: 40px;
  margin-bottom: 30px;
}

.btnShowMore {
  padding: 20px;
  background-color: transparent;
  border: 1px solid var(--accent);
  color: var(--accent);
  border-radius: 5px;

  &:hover,
  &:focus {
    background-color: var(--accent);
    color: var(--white);
  }
}
