@import "../../styles/mixins/media.scss";

.productList {
  display: flex;
  flex-wrap: wrap;
  margin-top: -20px;
  margin-left: -16px;

  @include tablet {
    margin-top: -30px;
    margin-left: -30px;
  }
}

.productItem {
  flex-basis: calc(100% / 2 - 16px);
  margin-top: 20px;
  margin-left: 16px;

  @include tablet {
    flex-basis: calc(100% / 2 - 30px);
    margin-top: 30px;
    margin-left: 30px;
  }

  @include largeTablet {
    flex-basis: calc(100% / 3 - 30px);
  }

  @include desktop {
    flex-basis: calc(100% / 4 - 30px);
  }
}
